<template>
	<div class="grid crud-demo">
		<div class="col-12">
			<div class="card">
				<Toast/>
				<Toolbar class="mb-4">
					<template v-slot:left>
						<div class="my-2">
							<Button label="New" icon="pi pi-plus" class="p-button-success mr-2" @click="openNew" />
							<Button label="Delete" icon="pi pi-trash" class="p-button-danger" @click="confirmDeleteSelected" :disabled="!selectedEntitys || !selectedEntitys.length" />
						</div>
					</template>

					<template v-slot:right>
						<FileUpload mode="basic" accept="image/*" :maxFileSize="1000000" label="Import" chooseLabel="Import" class="mr-2 inline-block" />
						<Button label="Export" icon="pi pi-upload" class="p-button-help" @click="exportCSV($event)"  />
					</template>
				</Toolbar>
				<DataTable ref="dt" :value="entitys" v-model:selection="selectedEntitys" dataKey="id" :paginator="true" :rows="10" :filters="filters"
							paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown" :rowsPerPageOptions="[5,10,25]"
							currentPageReportTemplate="Showing {first} to {last} of {totalRecords} entitys" responsiveLayout="scroll">
					<template #header>
						<div class="flex flex-column md:flex-row md:justify-content-between md:align-items-center">
							<h5 class="m-0">Administrar Notificaciones</h5>
							<span class="block mt-2 md:mt-0 p-input-icon-left">
                                <i class="pi pi-search" />
                                <InputText v-model="filters['global']" placeholder="Search..." />
                            </span>
						</div>
					</template>
					<Column selectionMode="multiple" headerStyle="width: 3rem"></Column>
					<Column field="name" header="Cliente" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Cliente</span>
							{{slotProps.data.venta.item.nameCustomer}}
						</template>
					</Column>					
					<Column field="type" header="Telefono" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Telefono</span>
							{{slotProps.data.venta.item.phone}}
						</template>
					</Column>
					<Column field="address1" header="Correo" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Correo</span>
							{{slotProps.data.venta.item.email}}
						</template>
					</Column>
					<Column field="city" header="Producto" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Producto</span>
							{{slotProps.data.venta.item.nameProduc}}
						</template>
					</Column>	
					<Column field="city" header="Tipo" :sortable="true">
						<template #body="slotProps">
							<span class="p-column-title">Tipo</span>
							{{slotProps.data.type}}
						</template>
					</Column>		
				</DataTable>
				<Dialog v-model:visible="entityDialog" :style="{width: '450px'}" header="Informacin Tenans" :modal="true" class="p-fluid">
						
					<template #footer>
						<Button label="Cancel" icon="pi pi-times" class="p-button-text" @click="hideDialog"/>
						<Button label="Save" icon="pi pi-check" class="p-button-text" @click="saveEntity" />
					</template>
				</Dialog>
				<Dialog v-model:visible="deleteEntityDialog" :style="{width: '450px'}" header="Confirm" :modal="true">
					<div class="flex align-items-center justify-content-center">
						<i class="pi pi-exclamation-triangle mr-3" style="font-size: 2rem" />
						<span v-if="entity">Desea eliminar un Usuario <b>{{entity.name}}</b>?</span>
					</div>
					<template #footer>
						<Button label="No" icon="pi pi-times" class="p-button-text" @click="deleteEntityDialog = false"/>
						<Button label="Yes" icon="pi pi-check" class="p-button-text" @click="deleteEntity" />
					</template>
				</Dialog>

				<Dialog v-model:visible="deleteEntitysDialog" :style="{width: '450px'}" header="Confirm" :modal="true">
					<div class="flex align-items-center justify-content-center">
						<i class="pi pi-exclamation-triangle mr-3" style="font-size: 2rem" />
						<span v-if="entity">Are you sure you want to delete the selected products?</span>
					</div>
					<template #footer>
						<Button label="No" icon="pi pi-times" class="p-button-text" @click="deleteEntitysDialog = false"/>
						<Button label="Yes" icon="pi pi-check" class="p-button-text" @click="deleteSelectedEntitys" />
					</template>
				</Dialog>	
			</div>
		</div>
	</div>

</template>

<script>
import NotificacioneService from '../service/NotificacioneService';

export default {
	data() {
		return {
			entitys: null,
			entityDialog: false,
			deleteEntityDialog: false,
			deleteEntitysDialog: false,
			entity: {},
			selectedEntitys: null,
			filters: {},
			submitted: false,
			statuses: [
				{label: 'INSTOCK', value: 'instock'},
				{label: 'LOWSTOCK', value: 'lowstock'},
				{label: 'OUTOFSTOCK', value: 'outofstock'}
			]
		}
	},
	notificacioneService: null,
	created() {
		this.notificacioneService = new NotificacioneService();
	},
	mounted() { 
		//this.NotificacioneService.getNotificaciones().then(data => this.entitys = data);
		this.loadEntitys();
	},
	methods: {		
		openNew() {
			this.entity = {};
			this.submitted = false;
			this.entityDialog = true;
		},
		hideDialog() {
			this.entityDialog = false;
			this.submitted = false;
		},
		saveEntity() {
			this.submitted = true;
			if (this.entity.name.trim()) {
				if (this.entity.id) {
					console.log("Tiene id");
					this.entity.status= "active";					
					this.notificacioneService.updateNotificacione(this.entity).then(()=>{
					this.$toast.add({severity:'success', summary: 'Successful', detail: 'Notificacione Actualizado', life: 3000});
					}).catch((data)=>{
						console.log(data.error);
						this.$toast.add({severity:'error', summary: 'Error Actualizando', detail: 'Error Notificacione Actualizado', life: 3000});	
					});
					this.loadEntitys();
				}else{					
					this.entity.status="active";
					this.notificacioneService.createdNotificacione(this.entity).then(()=>{
					this.$toast.add({severity:'success', summary: 'Successful', detail: 'Notificacione Creado con exito', life: 3000});
					}).catch((data)=>{
						console.log(data);
						this.$toast.add({severity:'error', summary: 'Error Creando ', detail: 'Error al creal el Notificacione', life: 3000});	
					});
				this.loadEntitys();
				}	
			}
			this.entityDialog = false;
			this.entity = {};
							
		},
		editEntity(entity){				
        this.entity = {...this.deleteAttribNull(entity)};
			this.entityDialog = true;
		},
		confirmDeleteEntity(entity) {
			this.entity = entity;
			this.deleteEntityDialog = true;			
		},
		deleteEntity() {
			this.notificacioneService.deleteNotificacione(this.entity).then(()=>{
					this.$toast.add({severity:'success', summary: 'Successful', detail: 'Notificacione Eliminado', life: 3000});
						this.loadEntitys();
					}).catch(()=>{						
						this.$toast.add({severity:'error', summary: 'Error Eliminando ', detail: 'Error Notificacione Actualizado', life: 3000});	
					})
			this.deleteEntityDialog = false;
			this.entity = {};
			
		},		
		exportCSV() {
			this.$refs.dt.exportCSV();
		},
		confirmDeleteSelected() {
			this.deleteEntitysDialog = true;
		},
		deleteSelectedEntity() {
			this.entitys = this.entitys.filter(val => !this.selectedEntitys.includes(val));
			this.deleteEntitysDialog = false;
			this.selectedentitys = null;
			this.$toast.add({severity:'success', summary: 'Successful', detail: 'Products Deleted', life: 3000});
		},
		loadEntitys(){
			this.notificacioneService.getNotificaciones().then(data => this.entitys = data);
			console.log(this.entitys);
		},
		deleteAttribNull(entity){
			Object.keys(entity).forEach(key => {
                    if (entity[key] === null) delete entity[key];
                 }); 
			return entity;	 
		}
	}
}
</script>

<style scoped lang="scss">
	.product-badge {
		border-radius: 2px;
		padding: .25em .5rem;
		text-transform: uppercase;
		font-weight: 700;
		font-size: 12px;
		letter-spacing: .3px;

		&.status-instock {
			background: #C8E6C9;
			color: #256029;
		}

		&.status-outofstock {
			background: #FFCDD2;
			color: #C63737;
		}

		&.status-lowstock {
			background: #FEEDAF;
			color: #8A5340;
		}
	}
</style>
