import axios from 'axios'

export default class NotificacioneService {
    url = 'http://localhost:8080/notificaciones/';
	createdVenta(Notificacione) {
    Date.now(); 
    const timeElapsed = Date.now();
    const today = new Date(timeElapsed);  
    Notificacione.date= today.toISOString();    
    return axios.post(this.url,Notificacione).then(res=>
        res.data        
      ); 
    }
    getNotificaciones() {  
        return axios.get(this.url).then(res=>
          res.data        
        ); 
      }

    updateNotificacione(Notificacione) {
      return axios.patch(this.url+Notificacione.id,Notificacione).then(res=>
      res.data        
    ); 
  }
  getNotificacione(Notificacione) {
    return axios.get(this.url+Notificacione.id).then(res=>
    res.data        
    ); 
  }
  deleteNotificacione(Notificacione) {
    return axios.delete(this.url+Notificacione.id).then(res=>
    res.data        
  ); 
}
}
